<template>
  <div id="invoice-top">
    <div class="content"
         style="margin-top:10px;">
      <div>发票类型</div>
      <div>{{ invoiceScale }}</div>
    </div>
    <div class="content">
      <div>抬头类型</div>
      <div>{{ invoiceType }}</div>
    </div>
    <div class="content">
      <div>发票抬头</div>
      <div>{{ buyerName }}</div>
    </div>
    <div class="content">
      <div>公司地址</div>
      <div>{{ address }}</div>
    </div>
    <div class="content">
      <div>公司税号</div>
      <div>{{ taxNum }}</div>
    </div>
    <div class="content">
      <div>建议开票金额</div>
      <div>{{ invoiceAmount }}</div>
    </div>
    <div v-if="currentInfo.invoiceScale == 3"
         class="content">
      <div>公司电话</div>
      <div>{{ companyTel }}</div>
    </div>
    <div v-if="currentInfo.invoiceScale == 3"
         class="content">
      <div>开户银行</div>
      <div>{{ bankName }}</div>
    </div>
    <div v-if="currentInfo.invoiceScale == 3"
         class="content">
      <div>开户账号</div>
      <div>{{ account }}</div>
    </div>

    <div class="content"
         style='margin-top:10px;'>
      <div>联系电话</div>
      <div>{{ phone }}</div>
    </div>
    <div v-if="currentInfo.invoiceScale == 2"
         class="content">
      <div>电子邮箱</div>
      <div>{{ email }}</div>
    </div>
    <van-button v-if="currentInfo.status == 1"
                type="default"
                class="Invoicing"
                @click="invoiceShow = true">确定开票</van-button>
    <van-button v-else
                disabled
                type="default"
                class="Invoicing Invoiced">已开票</van-button>
    <!-- 确定开票提示信息 -->
    <van-dialog v-model="invoiceShow"
                class=""
                confirm-button-text="继续"
                cancel-button-text="取消"
                title="提示"
                message='确认后，顾客将不可修改发票信息，是否继续？'
                show-cancel-button
                @confirm="confirm"
                v-cloak>
    </van-dialog>
  </div>
</template>
<script>
import { GetBusinessInvoiceByorderroom, GetBusinessInvoiceAccept } from '@/api/api_invoice';
import { GetSystemDictDataByType } from '@/api/api_query';
import { Toast } from 'vant';
export default {
    data() {
        return {
            currentInfo: {},
            invoiceScale: null,
            invoiceType: null,
            invoiceAmount: null,
            buyerName: '',
            taxNum: '',
            phone: '',
            address: '',
            email: '',
            account: '',
            bankName: '',
            companyTel: '',
            invoiceShow: false
        };
    },
    created() {
        this.$store.commit('setHeader', '开票详情');
        this.invoiceAmount = this.$route.query.invoiceAmount + '元';
        this.getInfo();
    },
    methods: {
        // 查询发票信息
        getInfo() {
            GetBusinessInvoiceByorderroom({ orderRoomId: this.$route.query.orderRoomId }).done((res) => {
                if (res.ret == 1 && res.data) {
                    this.currentInfo = res.data;
                    this.buyerName = this.currentInfo.buyerName;
                    this.taxNum = this.currentInfo.taxNum;
                    this.phone = this.currentInfo.phone;
                    this.address = this.currentInfo.address;
                    this.email = this.currentInfo.email;
                    this.account = this.currentInfo.account;
                    this.bankName = this.currentInfo.bankName;
                    this.companyTel = this.currentInfo.companyTel;
                    this.getAPI();
                }
            });
        },
        getAPI() {
            GetSystemDictDataByType({ dictType: 'business_invoice_scale' }).done((res) => {
                this.invoiceScale = res.data.find((item) => item.dictValue == this.currentInfo.invoiceScale).dictLabel;
            });
            GetSystemDictDataByType({ dictType: 'business_invoice_type' }).done((res) => {
                this.invoiceType = res.data.find((item) => item.dictValue == this.currentInfo.invoiceType).dictLabel;
            });
        },
        // 准备开票
        confirm() {
            GetBusinessInvoiceAccept({ orderInvoiceId: this.currentInfo.id }).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                this.getInfo();
            });
        }
    }
};
</script>
<style scoped>
#invoice-top {
    overflow-y: auto;
    height: 90%;
}
.content {
    display: flex;
    justify-content: space-between;
    padding: 10px 16px;
    background: #fff;
    border: 1px solid #fafafa;
    line-height: 30px;
    font-size: 0.8rem;
}
.Invoicing {
    background: #ff7c42;
    color: #fff;
    width: 90%;
    margin: 10px 0;
    border-radius: 5px;
}
.Invoiced {
    background: #fff;
    color: #000;
}
</style>



